/*** RESET ***/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
  scroll-behavior: smooth;
}
html {
  cursor: url('/images/chunkie_cursor.png'), auto;
}
* {
  box-sizing: border-box;
}

a {
  cursor: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg'  width='40' height='48' viewport='0 0 100 100' style='fill:black;font-size:24px;'><text y='50%'>ðŸ‘¾</text></svg>")
      16 0,
    auto;
}

::-webkit-scrollbar {
  display: none;
}

html,
body {
  overflow-x: hidden;
}

body {
  color: hsla(0, 0%, 20%, 1);
  font-family: 'Montserrat', sans-serif;
  background-color: hsla(0, 0%, 97%, 1);
  scroll-behavior: smooth;
  /* position: relative; */
}

.header {
  height: 100vh;
  max-width: 95%;
  margin: 0 auto;
}
.header-container {
  margin: auto;
  position: absolute;
  bottom: 10%;
  padding: 10px;
}

.body-container {
  /*max-width: 1200px;*/
  margin-left: auto;
  margin-right: auto;
  display: inline-flexbox;
}

section {
  padding: 10px 0px;
  margin-bottom: 10vh;
  align-content: center;
  background: hsla(0, 0%, 97%, 1);
  /* background: hsla(28, 89%, 89%, 1); */
}

section.home {
  height: auto;
  margin-right: auto;
  margin-left: auto;
  margin-bottom: 0;
}

h1 {
  font-size: 4em;
  font-weight: 900;
  letter-spacing: 0.01em;
  text-align: left;
  /*font-family: 'Raleway', sans-serif;*/
  font-family: 'Roboto Mono', monospace;
  color: hsla(0, 0%, 20%, 1);
}

h3 {
  text-align: left;
  margin: 0.4rem 0;
  font-size: 1.5em;
  letter-spacing: 1.5px;
  font-family: 'Roboto Mono', monospace;
  color: hsla(0, 0%, 20%, 1);
  font-weight: 400;
  hyphens: auto;
}

p {
  font-family: 'Montserrat', monospace;
  font-weight: 300;
  font-size: 18px;
  /*margin:3px 0 10px 0;*/
  line-height: 1.5rem;
  color: hsla(0, 0%, 20%, 1);
}

section.header {
  margin-left: auto;
  margin-right: auto;
  margin-top: 0;
  margin-bottom: 0;
  text-align: center;
}

section.header,
a {
  font-weight: 500;
  text-decoration: none;
  display: inline-block;
  color: hsla(0, 0%, 0%, 1);
  border-bottom: none;
  background: none;
  transition: all 0.2s;
  opacity: 1;
}

#about {
  max-width: 810px;
  width: 90%;
  min-width: 300px;
  font-size: 18px;
  margin-top: 1rem;
}

h2 {
  text-align: center;
  font-size: 24px;
  font-weight: 600;
  border-bottom: hsla(0, 0%, 20%, 1) 1px solid;
}

.row a {
  text-decoration: none;
  color: black;
  width: 100%;
}

/* Add padding BETWEEN each column */
.row,
.row > .column {
  padding: 0.5em;
  margin-right: auto;
  margin-left: auto;
}

/* Create four equal columns that floats next to each other */
.column {
  margin: 0 auto;
  float: left;
  width: 30%;
  display: flex;
  padding: auto 8px;
  display: block;
}

.background {
  position: relative;
}

.background::before {
  margin: 1.25em 4em 1.25em 3%;
  width: 97%;
  content: '';
  position: absolute;
  overflow: hidden;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  box-sizing: border-box;
}

.background.side-project::before {
  background: transparent url('/images/dots-side-project.svg') repeat bottom
    right;
}

.background.wsj::before {
  background: transparent url('/images/dots-wsj.svg') repeat bottom right;
}

.background.nbc::before {
  background: transparent url('/images/dots-nbc.svg') repeat bottom right;
}

.background.bi::before {
  background: transparent url('/images/dots-bi.svg') repeat bottom right;
}

.background.words::before {
  background: transparent url('/images/dots-words.svg') repeat bottom right;
}

.background.transit::before {
  background: transparent url('/images/dots-transit.svg') repeat bottom right;
}

.content {
  min-width: 100%;
  margin: 1em 0em 1em 0em;
  display: inline-block;
  overflow: hidden;
  position: relative;
  vertical-align: top;
  z-index: 2;
  box-sizing: border-box;
  background-color: hsla(0, 0%, 97%, 1);
  transition: transform 0.2s linear;
}

.content > img {
  -webkit-filter: grayscale(60%);
  filter: grayscale(60%);
  transition: all 0.2;
  margin-top: 0.5rem;
}

.content:hover > img {
  -webkit-filter: none;
  filter: none;
  transition: all 0.2s;
}

.content:hover {
  transform: translate(-12px, -12px);
}

figure {
  padding: 10px;
  border-width: 1px;
  border-style: solid;
  border-radius: 3px;
  border-color: hsla(0, 0%, 20%, 1);
}

.info {
  position: relative;
}

.description {
  font-size: 16px;
  margin-left: auto;
  margin-right: auto;
  text-align: left;
}

.description-home {
  font-size: 14px;
  font-weight: 200;
  line-height: 18px;
  text-align: center;
}

.left-half {
  float: left;
  width: 70%;
}

.right-half {
  float: right;
  width: 30%;
  text-align: right;
}

.skills {
  text-align: left;
  font-size: 10px;
  padding-bottom: 0.5em;
}

.date {
  text-align: right;
  font-size: 10px;
  padding-bottom: 0.5em;
  padding-right: 1px;
}

.nomargin {
  margin: 0;
}
/* Clear floats after rows */
.row:after {
  content: '';
  display: table;
  clear: both;
}

* {
  margin: 0;
  padding: 0;
}

.headline {
  color: hsla(0, 0%, 20%, 1);
  font-size: 8vw;
  font-weight: 400;
  display: flex;
  position: relative;
}

.currently {
  margin-right: auto;
  margin-left: auto;
  line-height: 1.2;
  font-weight: 300;
  font-size: 2.5vw;
  text-align: left;
}

.socials {
  font-size: 28px;
  margin-top: 1rem;
}

.socials > a {
  color: hsla(0, 0%, 20%, 1);
  padding: 0px 6px 2px 6px;
}

.socials > a:hover {
  color: hsla(210, 87%, 85%, 1);
}

@-moz-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -moz-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}
@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}
@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -moz-transform: translateY(0);
    -ms-transform: translateY(0);
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  40% {
    -moz-transform: translateY(-30px);
    -ms-transform: translateY(-30px);
    -webkit-transform: translateY(-30px);
    transform: translateY(-30px);
  }
  60% {
    -moz-transform: translateY(-15px);
    -ms-transform: translateY(-15px);
    -webkit-transform: translateY(-15px);
    transform: translateY(-15px);
  }
}

.arrow > i {
  display: block;
  color: hsla(0, 0%, 20%, 1);
}
.arrow {
  position: absolute;
  bottom: 0%;
  right: 5%;
  margin-left: -20px;
  width: 40px;
  height: 40px;
}

.bounce {
  -moz-animation: bounce 2s infinite;
  -webkit-animation: bounce 2s infinite;
  animation: bounce 2s infinite;
}
.noselect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.doodle {
  position: absolute;
  z-index: -10;
  margin: 0 auto;
  overflow: hidden;
}

/* Mobile */
@media (max-width: 550px) {
  .column {
    width: 100%;
    margin: 8px auto;
  }
  .content {
    margin: auto;
  }
  .background::before {
    margin: 0.25em 0em 0% 3%;
  }
}

@media (min-width: 550px) and (max-width: 925px) {
  .column {
    width: 50%;
    margin: 8px auto;
  }
}

@media (max-width: 800px) {
  .headline {
    font-size: 5rem;
  }
  .currently {
    font-size: 1.65rem;
  }
  .header-container {
    bottom: 10%;
  }
  .header {
    max-width: 100%;
  }
}

@media (max-width: 350px) {
  .header-container {
    bottom: 0%;
  }
}

@media (min-width: 800px) and (max-width: 1200px) {
  .headline {
    font-size: 10vw;
  }
  .currently {
    font-size: 3.2vw;
  }
}

@media (min-width: 1200px) {
  .headline {
    font-size: 8vw;
  }
  .currently {
    font-size: 2.5vw;
  }
}

.affiliationBackground {
  display: block;
  padding: 3px;
  margin: 0 0;
  width: 100%;
  height: 25px;
  border-radius: 3px;
}

figure > div.wsj {
  background: hsla(2, 100%, 69%, 0.3);
  border-radius: 2px;
  height: 25px;
}
figure > div.side-project {
  background: hsla(0, 0%, 85%, 0.6);
  border-radius: 2px;
  height: 25px;
}
figure > div.nbc {
  background: hsla(241, 88%, 90%, 0.3);
  border-radius: 2px;
  height: 25px;
}

figure > div.bi {
  background: hsla(209, 100%, 92%, 0.6);
  border-radius: 2px;
  height: 25px;
}

.affiliation {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 12px;
  /*margin: 3px 0 10px 0;*/
  line-height: 1.5rem;
  padding: 0 3px;
  display: inline-block;
  position: relative;
}

footer {
  margin: 0 auto;
}

p.currently a[href] {
  padding: 0;
  position: relative;
  color: hsla(0, 0%, 0%, 1);
  font-weight: 300;
}

p.currently a[href]:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40%;
  background-color: hsla(210, 81%, 94%, 1);
  z-index: -10;
  -webkit-transition: all 0.1s linear;
  -moz-transition: all 0.1s linear;
  transition: all 0.1s linear;
}

p.currently a[href]:hover {
  text-decoration: none;
}

p.currently a[href]:hover:after {
  background-color: hsla(210, 37%, 87%, 1);
}

.canvas {
  width: 100vw;
  height: 110vh;
  overflow-y: hidden;
  overflow-x: hidden;
  position: absolute;
  left: 0vw;
  top: 0;
  opacity: 0.1;
}
.canvas canvas {
  height: 200vw !important;
  width: 200vw !important;
  overflow-x: hidden;
}
